.sticky-message {
	position: relative;
	padding: $spacing-medium;
	color: #fff;
	text-align: center;
	background-color: $color-primary;
	&.is-error {
		background-color: $color-error;
	}
	&.is-warning {
		background-color: $color-warning;
	}
	&.is-success {
		background-color: $color-success;
	}
}

.sticky-message__close {
	position: absolute;
	top: 0;
	right: 0;
	display: flex;
	align-items: center;
	width: 3rem;
	height: 100%;
    padding: 0.875rem;
	cursor: pointer;
}
.sticky-message__svg {
    display: inline-block;
    width: 18px;
    height: 18px;
	fill: currentColor;
	transform: rotate(0deg);
	transition: transform 100ms $easing-linear;
	.sticky-message__close:hover & {
		fill: rgba(0, 0, 0, 0.35);
		transform: rotate(90deg);
		transition: transform 100ms $easing-linear;
	}
}