.input {
	position: relative;
}

.input--icon {
    .form-control {
    	padding-left: 34px;
    }	
}

.input--icon__block {
	position: absolute;
    display: flex;
    width: 34px;
	height: 100%;
    align-items: center;
    justify-content: center;
    // border-right: 1px solid #ddd;
    &:after {
		// content: '';
		position: absolute;
		right: 0;
		top: 3px;
		bottom: 3px;
		width: 1px;
		opacity: .5;
		background-color: rgba(212,212,212,0);
		background-image: linear-gradient(to top,rgba(212,212,212,0) 0,#d4d4d4 30%,#d4d4d4 70%,rgba(212,212,212,0) 100%);
    }
}

.input--icon__svg {
	width: 18px;
	height: 18px;
	fill: #63758F;
}

/* input--group */

.input--group {
	position: relative;
	display: flex;
	// display: table;
	// border-collapse: separate;
	width: 100%;

	/* Modifier */
	&--small {
		max-width: 150px;
	}
	&--medium {
		max-width: 220px;
	}

	& > .form-control {
		display: table-cell;
		position: relative;
		float: left;
		width: 100%;
	}

	& + & {
		margin-top: 1rem;
	}
}

.input--group__control {
	flex-grow: 1;
	order: 1;
}

.input--group__addon {
	display: flex;
	align-items: center;
	border: $border;
	border-radius: $border-radius;
	padding: 0 .75rem;
	font-size: $font-size-small;
	line-height: 1;
	text-align: center;
	// width: 1%;
	white-space: nowrap;
	vertical-align: middle;
	background-color: $color-background-primary;
	color: $color-text-tertiary;
	transition: all $duration $easing-linear;

	.table & {
		font-size: $font-size-x-small;
		padding: 0 $spacing-x-small;
	}
}

.input--group__addon--left {
	order: 0;
	border-top-right-radius: 0px;
	border-bottom-right-radius: 0px;
	border-right: 0;
	& ~ .input--group__control .form-control {
		border-top-left-radius: 0px;
		border-bottom-left-radius: 0px;
	}
}
.input--group__addon--right {
	order: 2;
	border-top-left-radius: 0px;
	border-bottom-left-radius: 0px;
	border-left: 0;
	& ~ .input--group__control .form-control {
		border-top-right-radius: 0px;
		border-bottom-right-radius: 0px;
	}
}

.input--group:hover .input--group__addon {
	border-color: #AEB6C3;
}

.input--group:focus .input--group__addon, .input--group:focus .input--group__addon {
	border-color: $color-text-secondary;
}