.toolbar-box {
	border: $border;
	border-radius: $border-radius;
}

.toolbar-box__body {
}

.toolbar-box__row {
	padding: 30px;
	background-color: #fff;
	border-bottom: $border;
	&:first-of-type {
		border-radius: $border-radius $border-radius 0 0;
	}
}

.toolbar-box__footer {
	padding: 20px 30px;
	background-color: $color-background-primary;
	border-radius: 0 0 $border-radius $border-radius;
}