.button-group {
	display: inline-block;
	vertical-align: middle;
}

.button-group__flex {
	display: flex;
	.btn {
		margin-left: -1px;
		border-radius: 0;
		&:first-of-type {
			margin-left: 0;
			border-radius: $border-radius 0 0 $border-radius;
		}
		&:last-of-type {
			border-radius: 0 $border-radius $border-radius 0;
		}
	}
	& .btn + .btn {
		margin-left: -1px;
	}
}