.btn {
	display: inline-block;
	padding: 0 1.5em;
	cursor: pointer;
	text-decoration: none;
	vertical-align: middle;
	user-select: none;
	white-space: normal;
	appearance: none;
	transition: all $duration $easing-linear;
	border-radius: $border-radius;
	/* modifier */
	font-family: $font-family;
	font-size: $font-size-base;
	line-height: 2.3;
	font-weight: 500;
	background-color: #fff;
	color: $color-text-secondary;
	border: 1px solid $color-border;
	&:hover,
	&:focus {
		text-decoration: none;
		background-color: #f0f0f0;
	}
	&:disabled {
		background-color: $color-disabled;
		color: #fff;
		cursor: not-allowed;
	}
	& + & {
		margin-left: 10px;
	}
	// & > span {
	// 	vertical-align: middle;
	// }
}

/* Modifier Theme */
.btn--cta {
	color: #fff;
	background-color: $color-cta;
	border-color: $color-cta;
	&:hover,
	&:focus {
		color: #fff;
		background-color: darken($color-cta,10%);
		border-color: darken($color-cta,10%);
	}
}

.btn--primary {
	color: #fff;
	background-color: $color-primary;
	border-color: $color-primary;
	&:hover,
	&:focus {
		color: #fff;
		background-color: darken($color-primary,10%);
		border-color: darken($color-primary,10%);
	}
}

.btn--success {
	color: #fff;
	background-color: $color-success;
	border-color: $color-success;
	&:hover,
	&:focus {
		color: #fff;
		background-color: darken($color-success,10%);
		border-color: darken($color-success,10%);
	}
}

.btn--warning {
	color: #fff;
	background-color: $color-warning;
	border-color: $color-warning;
	&:hover,
	&:focus {
		color: #fff;
		background-color: darken($color-warning,10%);
		border-color: darken($color-warning,10%);
	}
}

.btn--error {
	color: #fff;
	background-color: $color-error;
	border-color: $color-error;
	&:hover,
	&:focus {
		color: #fff;
		background-color: darken($color-error,10%);
		border-color: darken($color-error,10%);
	}
}

.btn--cancel {
	padding: 0 1em;
	background-color: #fff;
	color: $color-text-link;
	border-color: transparent;
	&:hover,
	&:focus {
		color: darken($color-text-link, 10%);
	}
}

.btn--inverse {
	padding: 0 1em;
	background-color: transparent;
	color: #fff;
	&:hover,
	&:focus {
		color: darken(#fff, 10%);
		background-color: transparent;
	}
}

.btn--borderless {
	border-color: transparent;
	&:hover,
	&:focus {
		border-color: transparent;
	}
}

/* Modifier Size */
.btn--x-small {
	padding: 0 0.75em;
	font-size: 13px;
	line-height: 2;
}

.btn--small {
	padding: 0 1em;
	font-size: 13px;
	line-height: 2;
}

.btn--medium {
	padding: 0 1.2em;
	font-size: 16px;
	line-height: 2;
}

.btn--large {
	padding: 0 1.7em;
	font-size: 16px;
	line-height: 2.65;
}

.btn--x-large {
	padding: 0 1.5em;
	font-size: 24px;
	line-height: 2.3;
}

.btn--wide {
	width: 100%;
	text-align: center;
}

.btn--disabled {
	background-color: $color-disabled;
	border-color: $color-disabled;
	pointer-events: none;
	&:hover,
	&:focus {
		background-color: $color-disabled;
		border-color: $color-disabled;
	}

}

.btn--pagination {
    // padding: 0 2em;
    font-weight: normal;
	color: #6A7B96;
	border-color: #C6CEDA;
	&:hover,
	&:focus {
		color: #6A7B96;
	}
}

/* Special modifier, only for the button which only have icon */
.btn--icon {
	padding: 0 1em;
}
.btn--icon-left {
	padding-right: 1.5em;
}
.btn--icon-right {
	padding-left: 1.5em;
}

.btn--icon__svg {
	height: 1.143em; // 16px
	width: 1.143em; // 16px
	vertical-align: text-bottom;
	fill: currentColor;
	.btn--icon-left & {
		margin-right: 0.5rem;
	}
	.btn--icon-right & {
		margin-left: 0.5rem;
	}
}


