.tooltip {
	padding: 1rem;
	color: #fff;
}
.tr-tooltip {
	display: none;
	position: absolute;
	z-index: 100;
	min-height: 34px;
	background: rgba(83, 92, 105, 0.9);
	color: #ffffff;
	font-size: 13px;
	padding: 8px 10px;
	border-radius: 3px;
	box-sizing: border-box;

	&:after{
		position: absolute;
		content: '';
	}

}
.tr-tooltip.top-left:after {
	right: 10px;
	top: 100%;
	border-top: 7px solid rgba(83, 92, 105, 0.9);
	border-left: 7px solid transparent;
	border-right: 7px solid transparent;
}
.tr-tooltip.top-middle:after {
	left: 50%;
	top: 100%;
	transform: translateX(-7px);
	border-top: 7px solid rgba(83, 92, 105, 0.9);
	border-left: 7px solid transparent;
	border-right: 7px solid transparent;
}
.tr-tooltip.top-right:after {
	left: 10px;
	top: 100%;
	border-top: 7px solid rgba(83, 92, 105, 0.9);
	border-left: 7px solid transparent;
	border-right: 7px solid transparent;
}
.tr-tooltip.bottom-left:after {
	right: 10px;
	top: 0;
	transform: translateY(-7px);
	border-bottom: 7px solid rgba(83, 92, 105, 0.9);
	border-left: 7px solid transparent;
	border-right: 7px solid transparent;
}
.tr-tooltip.bottom-middle:after {
	left: 50%;
	top: 0;
	transform: translate(-7px, -7px);
	border-bottom: 7px solid rgba(83, 92, 105, 0.9);
	border-left: 7px solid transparent;
	border-right: 7px solid transparent;
}
.tr-tooltip.bottom-right:after {
	left: 10px;
	top: 0;
	transform: translateY(-7px);
	border-bottom: 7px solid rgba(83, 92, 105, 0.9);
	border-left: 7px solid transparent;
	border-right: 7px solid transparent;
}
.tr-tooltip.left:after {
	top: 10px;
	border-top: 7px solid transparent;
	border-bottom: 7px solid transparent;
	right: -7px;
	border-left: 7px solid rgba(83, 92, 105, 0.9);
}
.tr-tooltip.right:after {
	top: 10px;
	border-top: 7px solid transparent;
	border-bottom: 7px solid transparent;
	left: -7px;
	border-right: 7px solid rgba(83, 92, 105, 0.9);
}