/* No class declaration */
@import "../1-abstracts/abstracts";

/* 
 * @Base CSS / HTML tag declaration
 */

@import url(https://fonts.googleapis.com/css?family=Roboto:400,300,500,700,100);
// @import 'https://fonts.googleapis.com/css?family=Roboto';

/* Set box-sizing globally to handle padding and border widths */
*,
*:before,
*:after {
  box-sizing: border-box;
}

/* Must be 100% for off canvas to work */
html { 
  position: relative;
  min-height: 100%; 
  margin: 0;
  padding: 0;
  font-size: 100%;
  line-height: $line-height-text;
  font-family: $font-family;
  color: $color-text-primary;
  font-feature-settings: "kern", "liga", "pnum";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
}

/* Default body styles */
body {
  min-height: 100vh;
  font-size: $font-size-base;
  background: #ffffff;
}

/* Get rid of gap under images by making them display: inline-block; by default */
img {
  display: inline-block;
  vertical-align: middle;
}

p {
    margin: 0;
    font-weight: inherit;
}

strong, b {
    font-weight: $font-weight-medium;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
    font-weight: $font-weight-medium;
}

h1 { font-size: 32px; }
h2 { font-size: 28px; }
h3 { font-size: 22px; }
h4 { font-size: 18px; }
h5 { font-size: 16px; }
h6 { font-size: 14px; }

// a, a:hover, a:focus, a:link, a:visited {
a {
    position: relative;
    font-weight: $font-weight-regular;
    text-decoration: none;
    color: $color-text-link;
    cursor: pointer;
}

audio, canvas, iframe, img, svg, video {
    vertical-align: middle;
}

button:active, button:focus, button:visited, a:active, a:focus, a:visited {
    outline: 0;
}

::placeholder,
::-moz-input-placeholder,
::-webkit-input-placeholder {
  color: #54698d;
  font-size: $font-size-base;
  font-weight: $font-weight-regular;
}

