.main-header {
	@include border-box;
	width: 100%;
	height: 5rem;
	padding: 20px 0;
	border-bottom: 1px solid #E7EBF2;
	background-color: #fff;
}

.main-header__heading {
	display: inline-block;
	font-size: 16px;
	font-weight: 400;
}

.main-navigation {
	width: 220px;
	background-color: #323D47;
    overflow: hidden;
	transition: all 500ms;
}
.main-navigation--closed {
	width: 0;
}

.main-section {
	flex: 1;
 	background-color: #F2F4F7;
}

.main-content {
	padding: $spacing-xx-large $spacing-xx-large;
}

.main-title {
	margin-bottom: $spacing-xx-large;
	font-size: 20px;
	font-weight: normal;
}

.main-subtitle{
	font-size: 14px;
	margin-bottom: $spacing-medium;
	font-weight: normal;
	display: flex;

	&--small{
		font-size: 12px;
		margin-bottom: $spacing-medium;
		font-weight: normal;
		display: flex;
	}
}

.main-separator{
	display: flex;
	flex:1;
	content: "";
	border-top: 1px solid $color-border;
	margin: 0.6rem 0 0 0.3rem;
}