
.clearfix {
    zoom: 1; /* For IE 6/7 (trigger hasLayout) */
    &:before, &:after {
      content: "";
      display: table;
    }
    &:after {
      clear: both;
    }
}

.pointer {
    cursor: pointer;
}

.text-white {
    color: #fff;
}
.text-light {
    color: #fff;
}

.text-italic {
    font-style: italic;
}
.text-medium {
    font-weight: 500;
}
.text-bold {
    font-weight: bold;
}

.bg-grey-light {
    background-color: #F9FBFD;
}

.bg-white{
    background-color: #fff;
}

.text-center {
    text-align: center;
}
.text-right {
    text-align: right;
}

.inline-block {
    display: inline-block;
}

.display-block {
    display: block;
}

.hide, .is-hide, div .is-hide, section .is-hide, article .is-hide {
    display: none;
}
.disabled, .is-disabled {
    pointer-events: none;
}

.full-height {
    height: 100%;
}
.full-width {
    width: 100%;
}

.ver-mid {
    vertical-align: middle;
}
.table-cell {
    display: table-cell;
}

.block-center {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.relative {
    position: relative;
}

.float-left {
    float: left;
}

.float-right {
    float: right;
}

.is-coret {
    text-decoration: line-through;
}

.ver-top {
    vertical-align: top;
}

.align-items-center {
    align-items: center;
}

.js-stick {
    position: fixed;
    top: 0;
}

.position--relative {position: relative;}
