/* 
 * d3-chart 
 */

.d3-chart-container {
  display: flex;
  justify-content: center;
}

.d3-bars {
  // fill: $traveloka-blue;
  fill: #309EE5;
  stroke: #309EE5;
  stroke-width: 1;
  fill-opacity: 0;
  stroke-opacity: 0;
  shape-rendering: crispEdges;
  cursor: pointer;
}

.d3-bars.is-hover, .d3-bars:hover {
  fill-opacity: 0.02;
  stroke-opacity: .5;
  cursor: pointer;
}
.d3-bars.is-active {
  fill-opacity: 0.1;
  stroke-opacity: .5;
  cursor: pointer;
}


.d3-outerCircle {
  stroke-width: 1px;
  &.is-hover {
    stroke-width: 2px;
    &.is-active {
      stroke-width: 1px;
    }
  }
}


/* 
 * d3-legend 
 */

.axis path {
  fill: none;
  stroke: #d0d0d0;
  stroke-width: 1px;
  shape-rendering: crispEdges;
}

.y.axis path {
  stroke: none;
}

.axis line {
  fill: none;
  stroke: none;
  stroke-width: 1;
  shape-rendering: crispEdges;
}

.axis text {
  font-size: 12px;
  color: #727272;
  fill: #727272;
}

.grid .tick {
  stroke: #d0d0d0;
  stroke-opacity: 0.3;
  shape-rendering: crispEdges;
}

.y.grid .tick:first-of-type {
  stroke-opacity: 1;
}

.grid path {
  stroke-width: 0;
}


.d3-legend {
  display: inline-block;
  padding: 5px 12px;
  background-color: #fff;
  border: 1px solid #CBD0D8;
  border-radius: $border-radius;
}

.d3-legend__item {
  display: inline-block;
  font-size: 13px;

  .statRectMark {
    width: 11px;
    height: 11px;
    margin-right: 9px;
    margin-top: -.15em;
  }

  & + & {
    margin-left: 18px;
  }
}

/* 
 * d3-tip 
 */

 .d3-tip {
  display: inline-block;
  width: 230px;
  padding: 10px 12px;
  font-size: 13px;
  color: #fff;
  background: rgba(83, 92, 105, 0.95);
  border-radius: 3px;
  box-shadow: 0px 3px 6px 0px #DAE2EE;
  transition: left 300ms ease, opacity 600ms ease;

  .statRectMark {
    width: 8px;
    height: 8px;
    margin-top: -.15em;
    margin-right: 10px;
    border-radius: 10rem;
  }
}
/* Creates a small triangle extender for the tooltip */
.d3-tip:after {
  content: '';
  position: absolute;
  top: 27px;
  left: -8px;
  box-sizing: border-box;
  display: inline;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 8px 8px 8px 0;
  border-color: transparent rgba(83, 92, 105, 0.95) transparent transparent;
}
.d3-tip.w:after {
  left: initial;
  right: -8px;
  border-width: 8px 0 8px 8px;
  border-color: transparent transparent transparent rgba(83, 92, 105, 0.95);
}

/* Style northward tooltips differently */
.d3-tip.n:after {
  margin: -1px 0 0 0;
  top: 100%;
  left: 0;
}

.d3-tip__heading {
  font-size: 13px;
  font-weight: bold;
  color: #fff;
  line-height: 1;
}

.d3-tip__divider {
  width: 100%;
  border-bottom: 1px dashed #fff;
  margin: 6px 0 6px;
  opacity: .2;
}

.d3-tip__data {
  @include clearfix;
  line-height: 1.5;
}

.d3-tip__data-title {
  float: left;
}
.d3-tip__data-number {
  float: right;
}