.switcher {
	display: block;
	input {
		display: none;
	}
}

.switcher__pill {
	width: 60px;
	padding: 3px;
	border: 1px solid #8391A6;
	border-radius: $border-radius-pill;
	background-color: #8391A6;
	transition: background-color $duration $easing-linear;
	cursor: pointer;
	&:hover {
		background-color: lighten(#8391A6, 5%);
		transition: background-color $duration $easing-linear;
	}

	.switcher input:checked + & {
		background-color: #5EA4E6;
		border-color: #5EA4E6;
	}

	.switcher.is-disabled &,
	.switcher.is-disabled input:checked + &, 
	.switcher input:disabled + &,
	.switcher input:checked:disabled + & {
		background-color: #CFCFCF;
		border-color: #CFCFCF;
	}
}

.switcher__pill-inside {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	height: 20px;
}

.switcher__panel {
	position: absolute;
	top: 0;
	left: 0;
	display: block;
	width: 20px;
	height: 20px;
	background-color: #fff;
	border-radius: $border-radius-pill;
	transition: all $duration $easing-move;
	.switcher input:checked + .switcher__pill & {
		left: 100%;
		margin-left: -20px;
		transition: all $duration $easing-move;
	}
}

.switcher__text {
	flex: 1;
	text-align: center;
    display: inline-block;
    font-size: 11px;
    font-weight: $font-weight-bold;
    color: #fff;
    line-height: 1;
    vertical-align: middle;
    pointer-events: none;
}

.switcher__text--left {
	margin-left: 4px;
	opacity: 0;
	transform: translateX(100%);
	transition: all $duration $easing-move;
	.switcher input:checked + .switcher__pill & {
		transform: translateX(0);
		opacity: 1;
		transition: all $duration $easing-move;
	}
}

.switcher__text--right {
	margin-right: 4px;
	opacity: 1;
	transform: translateX(0);
	transition: all $duration $easing-move;
	.switcher input:checked + .switcher__pill & {
		opacity: 0;
		transform: translateX(-100%);
		transition: all $duration $easing-move;
	}
}

