.grid-container {
	display: flex;
	align-items: stretch;
}

/* modifier */
.grid-container--center-flex {
	align-items: center;
	justify-content: center;
}

.grid-column {
	@include box-sizing;
	flex-grow: 1;
	flex-basis: 0;
	& + & {
		margin-left: 30px;
	}
}

.grid-one-third {
	flex-grow: 1;
	flex-basis: 0;
}

.grid-two-third {
	flex-grow: 2;
	flex-basis: 0;
}