/* form control */

::-webkit-input-placeholder {color: #DEDEDE;}
:-moz-placeholder { color: #DEDEDE; } /* Firefox 18- */
::-moz-placeholder { color: #DEDEDE; } /* Firefox 19+ */
:-ms-input-placeholder { color: #DEDEDE; }

.form-control {
	box-sizing: border-box;
	width: 100%;
	height: 34px;
	padding: 0 1rem;
	box-shadow: none;
	background-color: #fff;
	font-size: $font-size-small;
	font-weight: $font-weight-regular;
	// line-height: 32px;
	color: $color-text-primary;
	border: $border;
	border-radius: $border-radius;
	transition: all $duration $easing-linear;

	&:hover {
		border-color: #AEB6C3;
	}
	&:focus {
		border-color: $color-primary;
		outline: 0;
	}

	&.is-disabled, &:disabled, .form-element.is-disabled & {
		background-color: #F7F7F7;
		cursor: not-allowed;
	}
	
	&.is-error, .form-element.is-error & {
		border-color: $color-error;
	}

	.table & {
		height: 24px;
		font-size: $font-size-x-small;
		padding: 0 $spacing-x-small;
	}

}

/* form element */

.form-element {
	text-align: left;
	& + & {
		margin-top: 20px;
	}
}

.form-element__label {
	display: block;
	margin-bottom: $spacing-x-small;
	font-size: $font-size-medium;
}
.form-element__label--row {
	display: flex;
	align-items: baseline;
	justify-content: space-between;
}



.form-element__control {}

.form-element__help {
	display: none;
	margin-top: $spacing-x-small;
	font-size: $font-size-small;
	color: $color-text-secondary;

	.form-element.is-error & {
		display: block;
		color: $color-error;
	}

	.form-element.is-warning & {
		display: block;
		color: $color-warning;
	}

	.form-element.has-message & {
		display: block;
	}
}

/* modifier */

.form-element__control--inline {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.form-element__separator {
	flex: auto;
	margin: 0 10px;
	text-align: center;
}
