.tabv2 {}

.tabv2__nav {
	position: relative;
	border-bottom: $border;
}

.tabv2__trigger {
	display: inline-block;
	padding-top: $spacing-medium;
	padding-bottom: $spacing-medium;
	padding-right: $spacing-x-large;
	padding-left: $spacing-x-large;
	color: $color-text-secondary;
	font-weight: 500;
	// transition: 0.3s all cubic-bezier(0.65, 0.05, 0.36, 1);
	transition: 300ms all ease-out;

	&.is-active {
		color: $color-text-primary;
		transition: 300ms all ease-out;
	}
}

.tabv2__highlighter {
	position: absolute;
	left: 0;
	bottom: -1px;
	display: block;
	width: 100%;
	border-bottom: 2px solid $color-primary;
	transition: 300ms all ease-out;
}

.tabv2__window {
	position: relative;
    width: 100%;
    overflow: hidden;
    transition: 300ms all ease-out;
}

.tabv2__slider {
	@include clearfix;
	position: absolute;
	transition: 300ms all ease-out;
	overflow: auto;
}

.tabv2__pane {
	float: left;
}


