$progress-text-width: 44px;
$progress-text-height: 26px;
$progress-text-margin: 10px;
$progress-margin: ($progress-text-height + $progress-text-margin);

.progress {
	line-height: 0;
	padding-left: ($progress-text-width/2);
	padding-right: ($progress-text-width/2);
	padding-bottom: $progress-margin;

	&.progress--top {
		padding-top: $progress-margin;
		padding-bottom: 0;
	}

	&.progress--side {
		display: flex;
		align-items: center;
		padding-left: 0;
		padding-right: 0;
		padding-bottom: 0;
		line-height: inherit;
	}
}

.progress__container {
	position: relative;
	display: inline-block;
	width: 100%;
	height: 9px;
	border-radius: $border-radius-pill;
	background-color: #dedede;
}

.progress__fill {
	position: relative;
	float: left;
	width: 0;
	height: 100%;
	border-radius: $border-radius-pill;
	background-color: $color-primary;
	vertical-align: middle;
}

.progress__tip {
    position: absolute;
    top: 100%;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: $progress-text-width;
    height: $progress-text-height;
    margin-top: $progress-text-margin;
    margin-left: -($progress-text-width/2);
    font-size: $font-size-small;
    line-height: 1;
    color: #fff;
    background-color: #555555;
    border-radius: $border-radius;
	&:before {
		content: '';
		position: absolute;
		top: -4px;
		left: 50%;
		display: block;
		width: 10px;
		height: 10px;
		margin-left: -5px;
		background-color: #555555;
		border-radius: 1px;
		transform: rotate(45deg);
	}

	.progress--top & {
		top: auto;
		bottom: 100%;
		margin-top: 0;
		margin-bottom: $progress-text-margin;
		&:before {
			top: auto;
			bottom: -4px;
		}
	}
}

.progress__text {
    font-size: 30px;
    font-weight: $font-weight-medium;
    margin-left: $spacing-x-large;
}
