.badge {
    box-sizing: border-box;
    display: inline-block;
    padding: .25em 1.25em;
    font-size: 12px;
    color: #fff;
    border-radius: 100rem;
    font-weight: 500;
}

.badge--primary {background-color: $color-primary;}
.badge--warning {background-color: $color-warning;}
.badge--success {background-color: $color-success;}
.badge--error {background-color: $color-error;}
.badge--pink {background-color: #ec75ff;}