.tab {}

.tab-nav {
	position: relative;
	border-bottom: $border;

	.tab--outside & {
		border-bottom: 0;
	}
}

.tab-nav__trigger {
	display: inline-block;
	padding-top: $spacing-medium;
	padding-bottom: $spacing-medium;
	padding-right: $spacing-x-large;
	padding-left: $spacing-x-large;
	color: $color-text-secondary;
	transition: width $duration $easing-linear, left $duration $easing-linear;

	&.is-active {
		color: $color-text-primary;
		font-weight: 500;
		transition: width $duration $easing-linear, left $duration $easing-linear;
	}

	.tab--slim & {
		padding-left: 0;
		padding-right: 0;
		margin-left: $spacing-x-large;
		&:first-of-type {
			margin-left: 0;
		}
	}

	.tab--outside & {
		position: relative;
		margin-left: $spacing-xx-small;
		border: 1px solid $color-border;
	    border-radius: $border-radius $border-radius 0 0;
		background-color: $color-background-primary;
		&.is-active {
			background-color: #fff;
			border-bottom-color: #fff;
		}
		&:first-of-type {
			margin-left: 0;
		}
	}
}

.tab-nav__highlighter {
	position: absolute;
	left: 0;
	bottom: -1px;
	display: block;
	width: 100%;
	border-bottom: 2px solid $color-primary;
	transform: translateX(0);
	transition: all $duration $easing-linear;

	.tab--outside & {
		top: 0;
		bottom: auto;
		border-width: 3px;
		border-radius: $border-radius $border-radius 0 0;
		transition: none;
	}
}

.tab-pane {
	display: none;
	&.is-active {
		display: block;
	}
}

.tab-content {
	padding: $spacing-medium 0;

	.tab--outside & {
	    margin-top: -1px;
	    padding: $spacing-x-large;
	    border: 1px solid $color-border;
	    border-radius: 0 0 $border-radius $border-radius;
	}
}

