$tranDur: 300ms;

.body-dialog--open {
	overflow: hidden;
	position: relative;
	min-height: 100vh;
}

.dialog-overlay {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.7);
	opacity: 0;
	transition: opacity $tranDur;
	z-index: 11;
	pointer-events: none;
	transition-duration: $tranDur;
	
	.body-dialog--open & {
		opacity: 1;
		pointer-events: auto;
	}
}

.dialog {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 12;
	padding-top: 3rem;
	padding-bottom: 3rem;
	overflow-x: hidden;
	overflow-y: auto;
	pointer-events: none;

	&.dialog--open {
		pointer-events: auto;
	}
}

.dialog-flex {
	display: flex;
	align-items: center;
	justify-content: center;
    min-height: 100%;
}

$ver-padding: 1.5rem;
$hor-padding: 2.5rem;

.dialog-content {
	position: relative;
	z-index: 5;
	min-width: 150px;
	background-color: #fff;
	border-radius: $border-radius;
	box-shadow: 0px 15px 60px 2px rgba(0, 0, 0, 0.6);
	overflow: hidden;
	/* animation */
	transform: scale(0.95);
	opacity: 0;
	transition: all $tranDur;

	.dialog--open & {
		transform: scale(1);
		opacity: 1;
		transition: all $tranDur;
	}
}

.dialog-content__header {
	padding: $ver-padding $hor-padding;
	border-radius: $border-radius $border-radius 0 0;
	// border-bottom: 1px solid #eee;
}

.dialog-content__heading {
	font-size: 19px;
}

.dialog-content__subheading {
	margin-top: 1rem;
}


.dialog-content__notification {
	padding-right: $hor-padding;
	padding-left: $hor-padding;
	padding-top: 20px;
	padding-bottom: 20px;
	color: #fff;
	&--error {
		background-color: $color-error;
	}
}

.dialog-content__body {
	padding: $ver-padding $hor-padding;
	&.dialog-content__body--fwidth {
		padding-right: 0;
		padding-left: 0;
	}
}

.dialog-content__footer {
	padding: $ver-padding $hor-padding;
	text-align: right;
	border-radius: 0 0 $border-radius $border-radius;
	// border-top: 1px solid #eee;
}
















