$checkbox-icon-width: 18px;

.checkbox {

}

.checkbox--inline {
	.checkbox-item {
		display: inline-block;
	}
	.checkbox-item + .checkbox-item {
		margin-left: 1.5rem;
		margin-top: 0;
	}
}

.checkbox-item {
	position: relative;
	display: block;
    margin-bottom: 1rem;
	cursor: pointer;

	input[type="checkbox"] {
		display: none;
	}

	/* disabled state */
	input[type="checkbox"]:disabled,
	&.is-disabled
	{
		pointer-events: none;
	}

	.table & {
		display: inline-block;
		min-width: $checkbox-icon-width;
		min-height: $checkbox-icon-width;
		margin-bottom: 0;
	    vertical-align: middle;
	}

}

.checkbox-item__icon {
	@include box-sizing;
	position: absolute;
	// top: 1px; // aligning with .checkbox-item__label
	top: 0;
	left: 0;
	display: inline-block;
	width: $checkbox-icon-width;
	height: $checkbox-icon-width;
	vertical-align: top;
	/* style */
    border-width: 1px;
    border-style: solid;
    border-color: #9BACC4;
    border-radius: 2px;
    background-color: #FCFCFD;
    transition: all 80ms $easing-linear;
    &:after {
	    content: "";
	    position: absolute;
	    top: 4px;
	    left: 3px;
	    box-sizing: border-box;
	    display: inline-block;
	    width: 10px;
	    height: 6px;
	    transform: rotate(-45Deg);
	    border-left: 2px solid transparent;
	    border-bottom: 2px solid transparent;
	    transition: all 80ms $easing-linear;
    }

	/* hover */
    .checkbox-item:hover & {
		border-color: $color-primary;
		background-color: #EBF4FE;
		transition: all 80ms $easing-linear;
    }

	/* checked */
    .checkbox-item input[type="checkbox"]:checked + & {
		border-color: $color-primary;
		background-color: $color-primary;
		transition: all 80ms $easing-linear;
    	&:after {
	    	border-color: #fff;
		}
    }

    /* disabled state by Attr */
	/* disabled state by Class .is-disabled */

	/* unselected */
    .checkbox-item input[type="checkbox"]:disabled + &,
    .checkbox-item.is-disabled &
    {
		border-color: #CFCFCF;
		background-color: #F7F7F7;
		transition: all 80ms $easing-linear;
    }

	/* selected/checked */
    .checkbox-item input[type="checkbox"]:disabled:checked + &,
    .checkbox-item.is-disabled input[type="checkbox"]:checked + &
    {
		border-color: #CFCFCF;
		background-color: #F7F7F7;
		transition: all 80ms $easing-linear;
		&:after {
	    	border-color: #CFCFCF;
		}
    }
}

$checkbox-label-left: ($checkbox-icon-width + 14px);
.checkbox-item__label {
	display: inline-block;
	vertical-align: top;
	margin-left: $checkbox-label-left;
	line-height: $checkbox-icon-width;

	/* disabled state by Attr */
	/* disabled state by Class .is-disabled */
	.checkbox-item input[type="checkbox"]:disabled ~ &,
	.checkbox-item.is-disabled &
	{
		opacity: .5;
	}
}

.checkbox-item__label-heading {
	font-weight: 500;
	line-height: $checkbox-icon-width;
	& + .sub-text {
		margin-top: 4px;
		line-height: $line-height-text;
	}
}
