.radio {

}

.radio--inline {
	.radio-item {
		display: inline-block;
		margin-bottom: 0;
	}
	.radio-item + .radio-item {
		margin-left: 1.5rem;
		margin-top: 0;
	}
}

.radio-item {
	position: relative;
	display: block;
    margin-bottom: 1rem;
	cursor: pointer;

	input[type="radio"] {
		display: none;
	}

	/* disabled state */
	input[type="radio"]:disabled,
	&.is-disabled
	{
		pointer-events: none;
	}

}

$radio-width: 18px;
.radio-item__icon {
	@include box-sizing;
	position: absolute;
	// top: 1px; // aligning with .radio-item__label
	top: 0;
	left: 0;
	display: inline-block;
	width: $radio-width;
	height: $radio-width;
	vertical-align: top;
	/* style */
    border-width: 1px;
    border-style: solid;
    border-color: #9BACC4;
    border-radius: 50%;
    background-color: #FCFCFD;
    transition: all 150ms $easing-linear;
    &:after {
	    content: "";
	    position: absolute;
	    box-sizing: border-box;
	    display: inline-block;
	    background-color: $color-primary;
	    width: 12px;
	    height: 12px;
	    border-radius: 50%;
	    left: 2px;
	    top: 2px;
	    transform: scale(0);
	    transition: all 150ms $easing-linear;
    }

    .radio-item:hover & {
		border-color: $color-primary;
		background-color: #EBF4FE;
		transition: all 150ms $easing-linear;
    }

    .radio-item input[type="radio"]:checked + & {
		border-color: $color-primary;
		transition: all 150ms $easing-linear;
    	&:after {
	    	transform: scale(1);
		    transition: all 150ms $easing-linear;
		}
    }

    /* disabled state by Attr */
	/* disabled state by Class .is-disabled */

	/* unselected */
    .radio-item input[type="radio"]:disabled + &,
    .radio-item.is-disabled &
    {
		border-color: #CFCFCF;
		background-color: #F7F7F7;
		transition: all 150ms $easing-linear;
    }

	/* selected/checked */
    .radio-item input[type="radio"]:disabled:checked + &,
    .radio-item.is-disabled input[type="radio"]:checked + &
    {
		border-color: #CFCFCF;
		background-color: #F7F7F7;
		transition: all 150ms $easing-linear;
		&:after {
	    	background-color: #CFCFCF;
		}
    }
}

$radio-label-left: ($radio-width + 14px);
.radio-item__label {
	display: inline-block;
	vertical-align: top;
	margin-left: $radio-label-left;
	line-height: $radio-width;

	/* disabled state by Attr */
	/* disabled state by Class .is-disabled */
	.radio-item input[type="radio"]:disabled ~ &,
	.radio-item.is-disabled &
	{
		opacity: .5;
	}
}

.radio-item__label-heading {
	font-weight: 500;
	line-height: $radio-width;
	& + .sub-text {
		margin-top: 4px;
		line-height: $line-height-text;
	}
}


