.sink-island {
	padding: 2rem;
	background-color: #fff;
	border: 1px solid #dedede;
	border-radius: 4px;
	& + & {
		margin-top: 2rem;
	}
}

.sink-island__title {
	margin-bottom: 2rem;
}

.sink-group {
	& + & {
		margin-top: 2rem;
	}
}

.sink-group__heading {
	display: inline-block;
	font-size: 11px;
	font-weight: 800;
	color: $color-text-secondary;
	text-transform: uppercase;
	letter-spacing: 1px;
	margin-bottom: 1rem;
	border-bottom: 5px solid rgba(0,0,0,.05)
}