/**
 * The basic plumbing of the website; global classes, mixins and styles that can be used anywhere and at any time.
 * For example if we need to use color variables throughout the site,
 * then this is the directory that we would place that code.
 */

/* No class declaration */
@import "1-abstracts/abstracts";

/* Style from external, dont be modified */
@import "2-vendor/vendor";

/* Global styling */
@import "3-base/base";

/* Utilities styling */
@import "4-utilities/utilities";

/* Define section of page */
@import "5-layouts/layouts";

/* Reusable components, widget */
@import "6-components/components";
