.form-group {
	text-align: left;
	& + & {
		margin-top: 20px;
	}
}

.form-label {
	@include clearfix;
	margin-bottom: 7px;
	font-size: 15px;
	p {
		font-size: 15px;
	}
	a {
		font-size: 14px;
	}
}
.form-label-desc {
	margin-top: 3px;
	font-size: 13px;
	color: $color-text-secondary;
}

.form-group--inline {
	display: flex;
	justify-content: space-between;
	min-height: 34px;
	// align-items: center;
	.form-label {
		display: inline-block;
		margin-bottom: 0;
		// line-height: 32px;
	}

	& + & {
		margin-top: 30px;
	}
}

.form-group--inline__label {
	@include box-sizing;
	flex-basis: 45%;
	padding-right: 50px;
}
.form-group--inline__field {
	flex-basis: 55%;
	// min-width: 200px;
}

/* form-group-field */
.form-group-field {}
.form-group-field--date-range {
	display: flex;
	align-items: center;
}

.form-group-field--date-range__separator {
	flex: auto;
	margin: 0 10px;
}